
.wrapper {
    background-color: #efefef;
    width: 70%;
    max-width: 500px;
    max-height: 300px;
    padding: 10px 30px;
    border-radius: 10px;
    margin-top: 10px;
    box-shadow: 0px 1px 20px 11px rgb(84 84 84/ 20%);
}

.item {
    margin-top: 10px;

}
.checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.checkbox + label {
    position: relative;
    vertical-align: middle;
    padding: 0 0 0 30px;
    cursor: pointer;
}

/* –ß–µ–∫–±–æ–∫—Å –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–∏ –Ω–µ–∞–∫—Ç–∏–≤–µ–Ω */
.checkbox + label:before {
    content: '';
    position: absolute;
    top: 3px;
    left: 0;
    width: 19px;
    height: 18px;
    border: 2px solid #a1a1a1;
    border-radius: 10px;
}
/* –ß–µ–∫–±–æ–∫—Å –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–∏ –∞–∫—Ç–∏–≤–µ–Ω */
.checkbox + label:after {
    content: '';
    position: absolute;
    border-radius: 10px;

}

/* –§–æ–Ω —á–µ–∫–±–æ–∫—Å–∞ –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–∏ –∞–∫—Ç–∏–≤–µ–Ω */
.checkbox:checked + label:after {
    position: absolute;
    top: 5px;
    left: 2.5px;
    border: 1px solid #ccc;
    background-color: #05336e;
    height: 15px;
    width: 16px;
}

.button1{
    width: 120px;
    max-height: 60px;
    padding: 5px;
    background-color: #05336e;
    border-radius: 7px;
    color: #ccc;
    font-size: 16px;
    border: 0.5px solid #818181;
    box-shadow: 0px 1px 20px 11px rgb(84 84 84/ 20%);
}

.button{
    margin-top: 35px;
}
.button1:hover {
     transform: translateY(-0.5px);
    color: #eeeeee;
 }

.button1:active {
     transform: translateY(0.2px);
 }

@media (max-width: 450px) {
    .FormControlLabel {
        margin-bottom: 12px;
    }
}